@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search{margin-bottom:20px}.search__form{height:100%}.search__input{height:100%;width:60%;padding:5px 10px;background:transparent;border:none;border-bottom:2px solid #eece1a;font-size:1.3rem;color:#ffffff}@media (min-width: 768px){.search__input{width:250px}}.search button{height:95%;padding:0 10px;background:#eece1a;border:1px solid #eece1a;cursor:pointer;-webkit-transition:.3s ease-in-out;transition:.3s ease-in-out}.search button:hover{background:#000000;color:#ffffff}

.station{position:relative;height:350px;width:230px;margin:20px auto;z-index:1}@media (min-width: 768px){.station{width:250px}}.station__content{overflow:hidden;height:100%;display:flex;flex-direction:column;background-color:#000}.station::before,.station::after{content:'';position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(135deg, #d35602, #ffe402);-webkit-filter:blur(10px);filter:blur(10px);opacity:0;z-index:-1;-webkit-transition:.3s;transition:.3s}.station::after{-webkit-filter:blur(10px);filter:blur(10px)}.station:hover::before,.station:hover::after{opacity:1}.station__title{flex:2 1;display:flex;justify-content:center;align-items:center;font-size:1rem;color:#eece1a}@media (min-width: 768px){.station__title{font-size:2.2rem}}.station__address{flex:2 1;display:flex;justify-content:center;align-items:center;font-style:italic}.station__button{flex:1 1;width:150px;margin:0 auto}.station__button button{padding:10px;border-radius:10px;background:none;border:1px solid #eece1a;cursor:pointer;-webkit-transition:background .3s ease-in-out;transition:background .3s ease-in-out}.station__button button a{color:#ffffff;-webkit-transition:color .3s ease-in-out;transition:color .3s ease-in-out}.station__button button:hover{background:#eece1a}.station__button button:hover a{color:#000000;text-decoration:none}

.geolocation__title{font-size:1.8rem;margin-bottom:30px}

.icon{margin-left:5px;display:flex;justify-content:flex-end}@media (min-width: 576px){.icon{margin-left:0;flex:1 1}}.icon svg{font-size:2.5rem}@media (min-width: 576px){.icon svg{font-size:4rem}}@media (min-width: 576px){.icon svg{font-size:4rem}}.icon .happy{fill:green}.icon .sad{fill:red}.icon .neutral{fill:yellow}.icon .neutral-sad{fill:orange}

.station-result{display:flex;align-items:center;height:80px;font-size:1rem}@media (min-width: 576px){.station-result{font-size:1.2rem}}@media (min-width: 768px){.station-result{font-size:1.5rem}}.station-result__title{flex:2 1;display:flex}.station-result__name{width:50px;text-align:left;color:#eece1a}@media (min-width: 576px){.station-result__name{width:100px}}

.air-info{position:relative;max-width:500px;margin:30px auto;border-radius:30px;background:rgba(0,0,0,0.8);z-index:1}.air-info__content{overflow:hidden;height:100%;padding:20px 15px;border-radius:20px;display:flex;flex-direction:column;background-color:#000}@media (min-width: 576px){.air-info__content{padding:50px 30px}}.air-info::before,.air-info::after{content:'';position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(135deg, #d35602, #ffe402);-webkit-filter:blur(10px);filter:blur(10px);opacity:1;z-index:-1;-webkit-transition:.3s;transition:.3s}.air-info::after{-webkit-filter:blur(10px);filter:blur(10px)}.button-info{width:100px;padding:5px 10px;border:1px solid #000000;border-radius:5px;background-color:#eece1a;font-size:1.2rem;cursor:pointer;-webkit-transition:.3s ease-in-out;transition:.3s ease-in-out}.button-info:hover{background-color:#000000;color:#eece1a;border:1px solid #eece1a}

.App{position:relative;min-height:100vh;padding:50px 0 0 0;background-image:-webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.7)), to(rgba(0,0,0,0.7))),url(/air-quality/static/media/bcg.8251da4a.jpg);background-image:linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)),url(/air-quality/static/media/bcg.8251da4a.jpg);background-position:center;background-size:cover;text-align:center;font-family:'Open Sans', sans-serif;color:#ffffff}.App__title{margin:0;margin-bottom:20px;font-size:2.5rem;text-transform:capitalize}.subtitle{font-size:1rem;margin-bottom:30px}@media (min-width: 768px){.subtitle{font-size:1.8rem}}.footer{position:absolute;bottom:0;width:100%;text-align:center}.footer a{color:#eece1a;-webkit-transition:-webkit-filter .3s ease-in-out;transition:-webkit-filter .3s ease-in-out;transition:filter .3s ease-in-out;transition:filter .3s ease-in-out, -webkit-filter .3s ease-in-out}.footer a:hover{text-decoration:none;-webkit-filter:brightness(50%);filter:brightness(50%)}

