@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap&subset=latin-ext');

.App {
  position: relative;
  min-height: 100vh;
  padding: 50px 0 0 0;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url('./img/bcg.jpg');
  background-position: center;
  background-size: cover;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  color: #ffffff;

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-size: 2.5rem;
    text-transform: capitalize;
  }
}

.subtitle {
  font-size: 1rem;
  margin-bottom: 30px;

  @media(min-width: 768px) {
    font-size: 1.8rem;

  }
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;

  a {
    color: #eece1a;
    transition: filter .3s ease-in-out;

    &:hover {
      text-decoration: none;
      filter: brightness(50%);
    }
  }
}