.search {
    margin-bottom: 20px;

    &__form {
        height: 100%;
    }

    &__input {
        height: 100%;
        width: 60%;
        padding: 5px 10px;
        background: transparent;
        border: none;
        border-bottom: 2px solid #eece1a;
        font-size: 1.3rem;
        color: #ffffff;

        @media(min-width: 768px) {
            width: 250px;
        }
    }

    button {
        height: 95%;
        padding: 0 10px;
        background: #eece1a;
        border: 1px solid #eece1a;
        cursor: pointer;
        transition: .3s ease-in-out;



        &:hover {
            background: #000000;
            color: #ffffff;

        }
    }
}